.home-div{
    max-height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-auto-rows: 10px;
    grid-gap: .9rem;
    padding: .9rem .9rem;
    overflow-y: auto;

    .test-grid-1{
        grid-row: span 11/ auto;
    }
    .test-grid-2{
        
        grid-row: span 9/ auto;
    }
}   

