.restaurant-page-div{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 2rem calc(100% - 2rem);
}

.restaurant-page-menu{
    background: #d8345f;
    padding: 0 1rem;
    overflow: auto;
}

.restaurant-page-menu-ul{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}
.restaurant-page-menu-li{
    list-style-type: none;
    margin-right: 2rem;
   
    .restauran-page-menu-text{
        font-size: .9rem;
        color: white;
        white-space: nowrap;
        font-weight: 400;
    }
}


.restaurant-main-menu{
    width: 100%;
    height: 100%;
    overflow-y: auto;
}

.restaurant-page-menu-sections-li{
    list-style-type: none;
}

.restaurant-page-menu-sections-li-div{
    // border: 1px solid blue;
    padding: 1rem;

    .menu-sections-li-header{
        font-size: 1.2rem;
        margin-bottom: 1.5rem;
        text-align: center;
    }
}

.restaurant-page-menu-food-li{
    list-style-type: none;
    display: grid;
    grid-template-columns: 1fr .3fr;
    margin-bottom: 2rem;

    .menu-food-li-name{
        font-size: 1rem;
    }
    .menu-food-li-description{
        font-size: .8rem;
    }
    .menu-food-li-price{
        font-size: 1rem;
    }
}