.Main{
    width: 100%;
    height: 100vh;
    position: relative;
    display: grid; 
    grid-template-rows: 9rem calc(100vh - 13rem) 4rem;
    
}

.Main-row-1{
    display: grid;
    grid-template-rows: 1.5fr 3fr;
    background: #fff;
    box-shadow:  5px 5px 10px #e6e6e6;
    // border: 1px solid green;
    .Main-row-1-docker{
        grid-row: 2/3;
        overflow: auto;
    }

}
.Main-row-2{
    grid-row: 2/3;
    height: 100%;
    width: 100%;
}